import React from 'react';
import './app.scss';
import Main from '../main';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import PopupMain from '../popup/popup';
import {AuthProvider} from '../context/auth';
import Search from '../search';


const App = () =>{
    //add history to history.back
    return(
        <AuthProvider>
            <Router>
            <React.Fragment>
                <Route path='/' exact component={Main}/>
                <Route path='/tv/:id' exact
                render={({match,location})=>{
                    const { id } = match.params;
                    return <PopupMain id={id} mediaType={'tv'} location={location}/>;
                }}/>
                <Route path='/movie/:id' exact
                render={({match,location})=>{
                    const { id } = match.params;
                    return <PopupMain id={id} mediaType={'movie'} location={location}/>;
                }}/>
                <Route path='/search/' exact render={({history})=>{
                    return <Search history={history}/>;
                }}/>
                <Route path='/search/:input' exact
                render={({match,history})=>{
                    const { input } = match.params;
                    return <Search definput={input} history={history}/>;
                }}/>
            </React.Fragment>
            </Router>
        </AuthProvider>
    );
};


export default App;