import React, { useContext } from 'react';
import {AuthContext} from '../context/auth';
import {GoogleSignUp,SignOut} from '../../services/firebase';
import {NavDropdown} from 'react-bootstrap';
import googleLogo from './google.svg';

const NavAuth = () =>{
    const {currentUser} = useContext(AuthContext);
    return(
        currentUser ? <Logged/> : 
        <div className="nav-auth">
            <img src={googleLogo} alt='' className='google-login' 
            onClick={() => GoogleSignUp()}/>
        </div>
    )
};

const Logged = () =>{
    const {currentUser} = useContext(AuthContext);

    return(
        <div className="nav-auth link-inverse">
            <img className="rounded-circle google-photo" src={currentUser.photoURL} alt=''/>
            <NavDropdown title={currentUser.displayName.split(" ")[0]}>
                <NavDropdown.Item onClick={()=>SignOut()}>Logout</NavDropdown.Item>
            </NavDropdown>
        </div>
    );
}

export default NavAuth;