const addLatest = (item) =>{
    let latest = localStorage.getItem('Latest');
    const itemData = {
        id: item.id,
        name: item.name,
        type: item.type,
        poster: item.poster
    };
    if(latest !== null){
        const ll = `${JSON.stringify(itemData)}[[[${latest}`;
        latest = [...new Set(ll.split('[[['))].slice(0,3).join('[[[');
        localStorage.setItem('Latest', latest);
    }else{
        localStorage.setItem('Latest', JSON.stringify(itemData));
    };
};

const getLatest = () =>{
    const latest = localStorage.getItem('Latest');
    if(latest){
        return latest.split('[[[',3).map(item=>JSON.parse(item));
    }else{
        return [];
    }  
};

export{
    addLatest,
    getLatest
};