import React,{Suspense,useContext, useState, useEffect} from 'react';
import ContinueListTV from './continue--listTV';
import ContinueListMovie from './continue--listMovie';
import {AuthContext} from '../context/auth';
import {GetTvTime,useIfContinueExistTV,useIfContinueExistMovie,GetMovieTime} from '../../services/firebase';
import {ContinueLoading} from '../loading';

const MainContinue = () =>{
    const {currentUser} = useContext(AuthContext);
    const[booleanTV,setBooleanTV] = useState(false);
    const[booleanMovie,setBooleanMovie] = useState(false);
    const dataTV = useIfContinueExistTV(currentUser.uid);
    const dataMovie = useIfContinueExistMovie(currentUser.uid);
    useEffect(()=>{
        if(dataTV.data){
            setBooleanTV(dataTV.data);
        };
        if(dataMovie.data){
            setBooleanMovie(dataMovie.data);
        };
    },[dataTV,dataMovie])

    return (
        <div className="continue-list">
            {booleanTV || booleanMovie ? <h3>Продолжить</h3> : false}
            <TV bool={booleanTV} user={currentUser}/>
            <Movie bool={booleanMovie} user={currentUser}/>
        </div>

    );
};

const TV = ({bool,user}) =>{
    if(bool){
        const tvContinue = GetTvTime(user.uid);
        return(
            <Suspense fallback={<ContinueLoading/>}>
                <ContinueListTV promise={tvContinue}/>
            </Suspense>
        )
    };

    return false;
}

const Movie = ({bool,user}) =>{
    if(bool){
        const movieTime = GetMovieTime(user.uid);
        return(
            <Suspense fallback={<ContinueLoading/>}>
                <ContinueListMovie promise={movieTime}/>
            </Suspense>
        )
    };

    return false;
}

export default MainContinue;