import React,{useState,Suspense} from 'react';
import {searchMulti} from '../../services/theMovieDB';
import MainList from '../main/main-list';
import Nav from '../nav';
import {withRouter} from 'react-router-dom';

const Search = ({definput,history}) =>{
    const [input,setInput] = useState(definput);
    const handleInput = (e) => {
        setInput(e.target.value);
        if(input){
            history.push(`/search/${e.target.value}`);
        };
    };

    return(
        <React.Fragment>
            <Nav/>
            <div className="container search">
                <MainSearchForm input={input} handle={handleInput}/>
                <SearchList input={input}/>
            </div>
        </React.Fragment>
    );
};

const SearchList = ({input}) =>{
    if(input /* input.length > 2 */){
        const search = searchMulti(input);
        return (
            <Suspense fallback={<h3>Loading</h3>}>
                <MainList promise={search}/>
            </Suspense>
        )
    };
    return false;
};

const MainSearchForm = ({input,handle}) =>{
    return(
        <input
            autoFocus
            placeholder="Поиск" 
            onChange={handle}
            value={input}
        />
    );
};

export default withRouter(Search);