const cheerio = require('cheerio');

const _searchUrl = 'https://rezka.ag/index.php?do=search&subaction=search&q=';
const _searchProxy = 'https://api.allorigins.win/get?url=';
    // https://thingproxy.freeboard.io/fetch/
    // https://api.allorigins.win/get?url=
const _getProxy = 'https://cors-anywhere.herokuapp.com/';
const _OneTranslator = 'Неизвестно';
const _cdnSeries = 'https://rezka.ag/ajax/get_cdn_series/';

const find = (query) =>{
    const promise = fetch(`${_searchProxy}${encodeURIComponent(`${_searchUrl}${encodeURIComponent(query)}`)}`)
        .then(res=>res.json())
        .then(data=>{
            if(data.contents){
                const $ = cheerio.load(data.contents);
                return $('.b-content__inline_item-cover > a').attr('href');
            }else{
                return data;
            }});
    return wrapPromise(promise);
};

const findTranslators = (query) =>{
    const promise = fetch(`${_searchProxy}${encodeURIComponent(query)}`)
        .then(res=>res.json())
        .then(data=>{
            if(data.contents){
                const translators = [];
                let regex = '';
                const newData = {
                    translators,
                    id: query.match(/\/\d+/g)[0].substr(1),
                    movie: false
                };
                if(data.contents.match(/(initCDNMoviesEvents\()([0-9]*), ([0-9]*)/g)){
                    regex = (data.contents.match(/(initCDNMoviesEvents\()([0-9]*), ([0-9]*)/g))[0].match(/\d+/g)[1];
                    newData.movie = true;
                }else{
                    regex = (data.contents.match(/(initCDNSeriesEvents\()([0-9]*), ([0-9]*)/g))[0].match(/\d+/g)[1];
                };
                const $ = cheerio.load(data.contents);
                if(newData.movie){
                    translators.push({
                        id: regex,
                        name: _OneTranslator,
                        url: data.contents.match(/sof.tv.initCDNMoviesEvents(.*),"default_quality"/g)[0].match(/\[(.*)",/g)[0].slice(0, -1).replace(/\\/g,'')
                        }
                    );
                }else{
                    if($('.b-translator__item').length){
                        $('.b-translator__item').each(function(index,element){
                            translators.push({
                                id: $(element).attr('data-translator_id'),
                                name: $(element).text()
                                }
                            );
                        });
                    }else{
                        translators.push({
                            id: regex,
                            name: _OneTranslator
                            }
                        );
                    };
                };
                return newData;
            }else{
                return data;
            }});

    return wrapPromise(promise);
};

const getSeasonsSeries = (query,data) =>{
    const promise = fetch(`${_getProxy}${_cdnSeries}`,{
        method: "POST",
        headers: {
            "content-type": "application/x-www-form-urlencoded; charset=UTF-8",
            //maybe fix issue "Access-Control-Allow-Origin": "*"
        },
        body: "id="+data.id+"&translator_id="+query+"&action=get_episodes"
    })
        .then(res=>res.json())
        .then(data=>{
                const CHRepisodes = cheerio.load(data.episodes.split('\\').join(''));
                const CHRseasons = cheerio.load(data.seasons.split('\\').join(''));
                let lenghtOfSeasonsAndCounter = parseInt(CHRseasons('li').attr('data-tab_id'));
                const newData = [];
                CHRseasons('li').each((i,e)=>{
                    newData.push({
                        season: parseInt(CHRseasons(e).attr('data-tab_id')),
                        episodes: []
                    });
                });
                CHRepisodes('.b-simple_episode__item').each((i,e)=>{
                    const episode = CHRepisodes(e).attr('data-episode_id');
                    if(parseInt(episode) <= 1 && i>0){
                        lenghtOfSeasonsAndCounter++;
                        newData.filter((e)=>{
                            return e.season === lenghtOfSeasonsAndCounter;
                        })[0].episodes.push(episode);
                    }else{
                        newData.filter((e)=>{
                            return e.season === lenghtOfSeasonsAndCounter;
                        })[0].episodes.push(episode);
                    };
                });
                return newData;
            });

    return wrapPromise(promise);
};

const getVideo = (data) =>{
    const promise = fetch(`${_getProxy}${_cdnSeries}`,{
        method: "POST",
        headers: {
            "content-type": "application/x-www-form-urlencoded; charset=UTF-8",
        },
        body: "id="+data.resid+"&translator_id="+data.translator+"&season="+data.season+"&episode="+data.episode+"&action=get_stream"
    })
        .then(res=>res.json())
        .then(data=>data)

    return wrapPromise(promise);
};

const wrapPromise = (promise) => {
    let status = 'pending';
    let result;
    const suspender = promise.then(
        (res)=>{
            status = 'success';
            result = res;
        },
        (err) =>{
            status = 'error';
            result = err;
        }
    );
    const read = () => {
        switch (status) {
          case 'pending':
            throw suspender
          case 'error':
            throw result
          default:
            return result
        };
    };
    return{read};
};

export {
    find,findTranslators,getSeasonsSeries,getVideo
}