import React,{useState,Suspense,useEffect} from 'react';
import {find,findTranslators,getSeasonsSeries} from '../../services/rezka';
import './main-res.scss';
import Player from '../player';
import FilmPlayer from '../player/film-player';
import {TranslatorListLoading,SeasonListLoading} from '../loading';
import {Dropdown,DropdownButton} from 'react-bootstrap';
import ErrorBoundary from '../error-boundary';

//const scrollToRef = (ref) => window.scrollTo(0, ref.offsetTop)

const MainView = ({selected,res,continueData})=>{
    const search = find(selected.originalName);
    return(
        <ErrorBoundary>
            <Suspense fallback={<TranslatorListLoading/>}>
                <Search promise={search} res={res} info={selected} continueData={continueData}/>
            </Suspense>
        </ErrorBoundary>
    )
};

const Search = ({promise,res,info,continueData})=>{
    const resource = promise.read();
    const translators = findTranslators(resource);
    return (
        <Suspense fallback={<TranslatorListLoading/>}>
            <Translators promise={translators} res={res} info={info} continueData={continueData}/>
        </Suspense>
    )
};

const Translators = ({promise,res,info,continueData}) =>{
    const[translator,setTranslator] = useState(null);
    const handleTranslator = (e) => setTranslator(e);
    const resource = promise.read();
    const episodes = getSeasonsSeries(translator,resource);
    useEffect(()=>{
        if(continueData){
            setTranslator(continueData.translator);
        };
    },[continueData])
    if(resource.movie){
        const movieData = continueData ? {
            id: continueData.id,
            resid: continueData.resid,
            translator: 'Неизвестно',
            res,
            name: info.name,
            poster: info.poster,
            time: continueData.time
        } :  {
            id: info.id,
            resid: resource.id,
            translator: 'Неизвестно',
            res,
            name: info.name,
            poster: info.poster,
        };
        return(
            <div className="mt-4">
                <div className="brdt mb-4"></div>
                <div className="text-light">
                    <h4 className='text-opacity mb-4'>Фильм</h4>
                    <FilmPlayer
                    data={movieData}
                    url={resource.translators[0].url} 
                    />
                </div>
            </div>
        );
    };
    return (
        <React.Fragment>
        <div className="mt-4">
            <div className="brdt mb-4"></div>
            <div className="service-content text-light">
                <h4 className='text-opacity mb-4'>Переводы</h4>
                <ListOfTranslators data={resource} handleTranslator={handleTranslator} continueData={continueData}/>
            </div>
        </div>
        {translator ? (
        <div className="mt-4">
            <div className="brdt mb-4"></div>
            <Suspense fallback={<SeasonListLoading/>}>
                <ListOfEpisodes promise={episodes} translator={translator}
                 id={resource.id} res={res} info={info}
                 continueData={continueData}/>
            </Suspense>
        </div>
        ) : false
        }
        </React.Fragment>
    );
};

const ListOfTranslators = ({data,handleTranslator,continueData}) =>{
    const[active,setActive] = useState(null);
    const handleActive = (e) => setActive(e.id);
    useEffect(()=>{
        if(continueData){
            setActive(continueData.translator);
        };
    },[continueData])
    return(
        <ul className="list-group list-group-horizontal-sm ul-translators mb-4">
        {
        data.translators.map((e)=>(
            <li className={`list-group-item ${e.id === active ? 'trs-active' : ''}`} 
            key={e.id}>
                <span onClick={()=>{handleActive(e);handleTranslator(e.id)}}>{e.name}</span>
            </li>
        ))
        }
        </ul>
    );
};

const ListOfEpisodes = ({promise,translator,id,res,info,continueData}) =>{
    const resource = promise.read();
    const[activeSeason,setActiveSeason] = useState();
    const[activeEpisode,setActiveEpisode] = useState();
    const[video,setVideo] = useState();
    useEffect(()=>{
        if(continueData){
            setActiveSeason(continueData.season);
            setActiveEpisode(continueData.episode);
            setVideo({
                resid: continueData.resid,
                id: info.id,
                translator,
                season: continueData.season,
                episode: continueData.episode,
                res,
                name: info.name,
                poster: info.poster,
                time: continueData.time
            });
        };
    },[continueData, info.id, info.name, info.poster, translator, res])
    const handleSeasonEpisode = (season,episode) =>{
        setActiveSeason(season);
        setActiveEpisode(episode);
    };
    const handleVideo = (resid,season,episode) => {
        const data = {
            resid,
            id: info.id,
            translator,
            season,
            episode,
            res,
            name: info.name,
            poster: info.poster
        };
        setVideo(data);
    };
    return(
        <React.Fragment>
        <div className="series-content text-light mb-3">
            <h4 className='text-opacity mb-4'>Сезон</h4>
            <div className='flex'>
            {resource.map(e=>(
                <DropdownButton drop='down' key={e.season} className={`mr-2 ${e.season === activeSeason ? 'sne-active' : ''}`}
                id="dropdown-basic-button" 
                title={`Сезон ${e.season}`}>
                {e.episodes.map(eSecond=>(
                        <Dropdown.Item key={eSecond} 
                        className={`text-light text-opacity ${eSecond === activeEpisode && e.season === activeSeason ? 'sne-active' : ''}`}
                        onClick={()=>{
                            handleVideo(id,e.season,eSecond);
                            handleSeasonEpisode(e.season,eSecond)
                        }}>
                        {eSecond}</Dropdown.Item>
                ))}
                </DropdownButton>
            ))}
            </div>
        </div>
        {video ? <Player data={video}/> : false}
        </React.Fragment>
    );
};

export default MainView;