import React,{useContext,useEffect} from 'react';
import {AuthContext} from '../context/auth';
import {SetMovieTime} from '../../services/firebase';

const FilmPlayer = ({url,data}) =>{
    const {currentUser} = useContext(AuthContext);
    let time = 0.0;
    function receiveMessage(event){
        if(currentUser){
            time = event.data.time;
        };
    };

    useEffect(() => {
        window.addEventListener('message', receiveMessage);
        const loop = setInterval(function(){
            if(currentUser){
                SetMovieTime(currentUser.uid,'movie',
                data.id,data.resid,
                time,data.translator,data.res,
                data.name,data.poster);
            }
        }, 2000);
    
        return () => {
          window.removeEventListener('message', receiveMessage);
          clearInterval(loop);
        };
    });

    return(
        <div className='playerjs'>
            <iframe
            id="playerplayer"
            title='playerjs'
            allowFullScreen
            className='player fade-in'
            src={'/playerjs.html?file='+url+(data.time ? `&start=${data.time}` : '')}
            type='text/html'
            allowtransparency='true'
            />
        </div>
    );
}

export default FilmPlayer;