import React from 'react';
import * as localService from '../../services/localStorage';
import { Link } from 'react-router-dom';

const MainList = ({promise,resource = null,title,history}) =>{
    resource = promise ? promise.read() : resource;

    return(
        <div className="main-list">
            <h3>{title}</h3>
            <div className="main-list__grid">
            {resource.map((item)=>(
                <div className="card" key={item.id}>
                    <Link to={`/${item.type}/${item.id}`}
                        onClick={()=>localService.addLatest(item)}/>
                    <div className="card-shadow"/>
                    <img alt="" src={item.poster}/>
                    <p>{item.name}</p>
                </div>
            ))}
            </div>
        </div>
    );
};

export default MainList;