const _apiKey = '?api_key=51d93e8a2a1a27a409fc155a8ece4e79';
const _baseUrl = 'https://api.themoviedb.org/3/';
const _ruLang = '&language=ru-RU';
const _noAdult = '&include_adult=false';
const posterpath = 'https://image.tmdb.org/t/p/w500';
const backdroppathWorst = 'https://image.tmdb.org/t/p/w1066_and_h600_bestv2';
const backdroppathOriginal = 'https://image.tmdb.org/t/p/original';

const getTV = (query) =>{
    const promise = fetch(`${_baseUrl}tv/${query}${_apiKey}${_ruLang}`)
        .then(res=>res.json())
        .then(_transfromMulti);
    
    return wrapPromise(promise);
};

const getMovie = (query) =>{
    const promise = fetch(`${_baseUrl}movie/${query}${_apiKey}${_ruLang}`)
        .then(res=>res.json())
        .then(_transfromMulti);
    
    return wrapPromise(promise);
};

const searchMulti = (query) =>{
    const promise = fetch(`${_baseUrl}search/multi${_apiKey}${_ruLang}&query=${query}&page=1${_noAdult}`)
        .then(res=>res.json())
        .then(data=>_noActor(data.results).map(_transfromMulti)
                    .slice(0, 30));
    
    return wrapPromise(promise);
};

const findPopular = () =>{
    const promise = fetch(`${_baseUrl}trending/all/week${_apiKey}${_ruLang}`)
        .then(res=>res.json())
        .then(data=>data.results.map(_transfromMulti)
                    .slice(0, 10));
    
    return wrapPromise(promise);
};

const wrapPromise = (promise) => {
    let status = 'pending';
    let result;
    const suspender = promise.then(
        (res)=>{
            status = 'success';
            result = res;
        },
        (err) =>{
            status = 'error';
            result = err;
        }
    );
    const read = () => {
        switch (status) {
          case 'pending':
            throw suspender
          case 'error':
            throw result
          default:
            return result
        };
    };
    return{read};
};

const _transfromMulti = (item) =>{
    return{
        id: item.id.toString(),
        //id: item.id
        name: item.name || item.title,
        // title: item.title,
        type: item.media_type,
        poster: item.poster_path ? `${posterpath}${item.poster_path}` : null,
        backdrop: `${backdroppathWorst}${item.backdrop_path}`,
        backdropOriginal: `${backdroppathOriginal}${item.backdrop_path}`,
        description: item.overview,
        releaseDate: item.release_date,
        originalName: item.original_name || item.original_title,
        // originalTitle: item.original_title
    }
};

const _noActor = (object) =>{
    return object.filter((el)=>{
        return el.known_for_department === undefined;
    });
};

export{
    searchMulti,
    findPopular,
    getTV,
    getMovie
};