import React,{Suspense,useState,useContext} from 'react';
import {MainView} from '../resources';
import * as movieService from '../../services/theMovieDB';
import { Link } from 'react-router-dom';
import './popup.scss';
import {AuthContext} from '../context/auth';
import {GetContinueTV,GetContinueMovie} from '../../services/firebase';

const PopupMain = ({id,mediaType,location})=>{
    const {currentUser} = useContext(AuthContext);
    const ifContinue = location.state ? location.state.item : null;
    const continueData = currentUser ? ( mediaType === 'tv' ? GetContinueTV(currentUser.uid,id) : GetContinueMovie(currentUser.uid,id) ) : null;
    
    return(
        <Suspense fallback={false}>
            <ContinueDataTest user={currentUser} promise={continueData} id={id} mediaType={mediaType} ifContinue={ifContinue}/>
        </Suspense>
    )
};

const ContinueDataTest = ({id,mediaType,promise,ifContinue,user}) =>{
    const resource = user ? (ifContinue ? ifContinue : promise.read()) : null;

    if(mediaType === 'tv'){
        const tv = movieService.getTV(id);
        return(
            <Suspense fallback={<h1>Loading</h1>}>
                <Popup promise={tv} continueData={resource}/>
            </Suspense>
        )
    }else if(mediaType === 'movie'){
        const movie = movieService.getMovie(id);
        return(
            <Suspense fallback={<h1>Loading</h1>}>
                <Popup promise={movie} continueData={resource}/>
            </Suspense>
        )
    }
}

const Popup = ({promise,continueData}) =>{
    const resource = promise.read();
    return(
        //<React.Fragment>
        <div className='local-bootstrap'>
        <div className='backdrop'>
            <img alt='' src={resource.backdropOriginal}/>
        </div>
        <div className='popup-info font-weight-light text-light shadow-sm'>
            <Link className='back-span text-opacity p-4' to='/'><span>X</span></Link>
            <div className='blur'></div>
            <div className='backdrop-content p-4 text-opacity'>
                <h1>{resource.name}</h1>
                <p className='description'>{resource.description}</p>
            </div>
        </div>
        <div className='resources p-4'>
            <h4 className='text-light text-opacity mb-4'>Сервис</h4>
            <Resources selected={resource} continueData={continueData}/>
        </div>
        </div>
        //</React.Fragment>
    );
};

const Resources = ({selected,continueData}) =>{
    const[res,setRes] = useState(continueData ? continueData.resource : '');
    const handleRes = (value) => {
        setRes(value);
    };
    const ress=[
        {value: 'hdrezka', name: 'HD REZKA', id: 'rr0', className: 'hdrezka-service'},
        {value: 'gidonline', name: 'GID ONLINE', id: 'rr1', className: 'gidonline-service'},
        //{value: 'filmix', name: 'FILMIX', id: 'rr2', className: 'filmix-service'}
    ];
    const Switcher = () =>{
        switch(res){
        case 'hdrezka':
            return <MainView res={res} selected={selected} continueData={continueData}/>;
        case 'gidonline':
            return false;
        case 'filmix':
            return false;
        default:
            return null;
        };
    };

    return(
        <React.Fragment>
        <div className='flex'>
        {
            ress.map(e=>
            <button className={`service shadow font-weight-bold text-light mr-3 ${e.className}`} 
            key={e.id} onClick={()=>handleRes(e.value)}>
                <span className='service-p text-opacity'>{e.name}</span>
            </button>
            )
        }
        </div>
        <Switcher/>
        </React.Fragment>
    );
};

export default PopupMain;