import * as firebase from 'firebase/app';
import 'firebase/auth';
import 'firebase/firebase-firestore';
import config from '../config/firebase';
import {useState,useMemo,useEffect,useCallback} from 'react';

const app = firebase.initializeApp(config);
const db = firebase.firestore();
const provider = new firebase.auth.GoogleAuthProvider();

const useRequest = (request) =>{
    const initialState = useMemo(()=>({
        data: null,
        loading: true,
        error: null
    }),[]);

    const [ dataState , setDataState ] = useState(initialState);

    useEffect(()=>{
        setDataState(initialState);
        let cancelled = false;
        request()
            .then(data=>!cancelled && setDataState({
                data,
                loading:false,
                error:null
            }))
            .catch(error=>!cancelled && setDataState({
                data: null,
                loading: false,
                error
            }));
        return () => cancelled = true;
    },[request, initialState]);

    return dataState;
};

const GoogleSignUp = () => {
    firebase.auth().signInWithPopup(provider);
};

const SignOut = () => {
    app.auth().signOut();
};

const SetMovieTime = (uid,type,id,resid,time,translator,resource,name,poster) =>{
    // const userId = firebase.auth().currentUser.uid;
    return db.collection('continueData').doc(uid).collection(type).doc(id).set({
        id,
        resid,
        type,
        translator,
        time,
        resource,
        date: firebase.firestore.Timestamp.now(),
        name,
        poster
    });
};

const SetTvTime = (uid,type,id,resid,season,episode,time,translator,resource,name,poster) =>{
    // const userId = firebase.auth().currentUser.uid;
    return db.collection('continueData').doc(uid).collection(type).doc(id).set({
        id,
        resid,
        type,
        translator,
        season,
        episode,
        time,
        resource,
        date: firebase.firestore.Timestamp.now(),
        name,
        poster
    });
};

const IfContinueExistTV = (uid) =>{
    const data = db.collection('continueData').doc(uid).collection('tv').get()
    .then(snap => {
        return Boolean(snap.size);
    });
    return data;
};

const IfContinueExistMovie = (uid) =>{
    const data = db.collection('continueData').doc(uid).collection('movie').get()
    .then(snap => {
        return Boolean(snap.size);
    });
    return data;
};

const useIfContinueExistTV = (uid) =>{
    const request = useCallback(
        ()=>IfContinueExistTV(uid),[uid]);
    return useRequest(request);
};

const useIfContinueExistMovie = (uid) =>{
    const request = useCallback(
        ()=>IfContinueExistMovie(uid),[uid]);
    return useRequest(request);
};


const GetContinueTV = (uid,id) =>{
    const promise = db.collection('continueData').doc(uid).collection('tv').doc(id).get()
        .then(data => {
            if(data.data()){
                return data.data()
            }else{
                return null;
            }})
    return wrapPromise(promise);
}

const GetContinueMovie = (uid,id) =>{
    const promise = db.collection('continueData').doc(uid).collection('movie').doc(id).get()
        .then(data => {
            if(data.data()){
                return data.data()
            }else{
                return null;
            }})
    return wrapPromise(promise);
}

const GetTvTime = (uid) =>{
    const promise = db.collection('continueData').doc(uid).collection('tv').orderBy("date", "desc")
    .get().then(data =>
        data.docs.map(doc => doc.data())
    );
    //asc
    return wrapPromise(promise);
};

const GetMovieTime = (uid) =>{
    const promise = db.collection('continueData').doc(uid).collection('movie').orderBy("date", "desc")
    .get().then(data =>
        data.docs.map(doc => doc.data())
    );
    //asc
    return wrapPromise(promise);
};

const wrapPromise = (promise) => {
    let status = 'pending';
    let result;
    const suspender = promise.then(
        (res)=>{
            status = 'success';
            result = res;
        },
        (err) =>{
            status = 'error';
            result = err;
        }
    );
    const read = () => {
        switch (status) {
          case 'pending':
            throw suspender
          case 'error':
            throw result
          default:
            return result
        };
    };
    return{read};
};

export{
    app,
    GoogleSignUp,
    SignOut,
    SetTvTime,
    GetTvTime,
    useIfContinueExistTV,
    useIfContinueExistMovie,
    GetContinueTV,
    GetContinueMovie,
    SetMovieTime,
    GetMovieTime
};