import React from 'react';
import * as localService from '../../services/localStorage';
import { Link } from 'react-router-dom';

const ContinueListTV = ({promise,resource = null,title,history}) =>{
    resource = promise ? promise.read() : resource;

    return(
        <>
        <h4>Сериалы</h4>
        <div className="continue-list__grid">
        {resource.map((item)=>(
            <div className="continue-card" key={item.id}>
                <Link to={{
                    pathname: `/${item.type}/${item.id}`,
                    state: {
                        item
                    }
                }} onClick={()=>localService.addLatest(item)}/>
                <img alt="" src={item.poster}/>
                <div className="info">
                    <p>{item.name}</p>
                    <div className="info-tv">
                        <p>Сезон {item.season}</p>
                        <p>Эпизод {item.episode}</p>
                    </div>
                </div>
            </div>
        ))}
        </div>
        </>
    );
};

export default ContinueListTV;