import React,{Suspense,useContext} from 'react';
import * as movieService from '../../services/theMovieDB';
import * as localService from '../../services/localStorage';
import MainList from './main-list';
import MainContinue from './main-continue';
import {AuthContext} from '../context/auth';

const MainRecommend = () =>{
    const popular = movieService.findPopular();
    const localData = localService.getLatest();
    const {currentUser} = useContext(AuthContext);

    return(
        <div className="container">
            {currentUser ? <MainContinue/> : false}
            {localData[0] ? <MainList resource={localData} title={'Недавние'}/> : false}
            <Suspense fallback={<h3>Loading</h3>}>
                <MainList promise={popular} title={'Популярное'}/>
            </Suspense>
        </div>
    );
};

export default MainRecommend;