import React,{Suspense,useContext,useEffect} from 'react';
import {getVideo} from '../../services/rezka';
import {PlayerLoading} from '../loading';
import {AuthContext} from '../context/auth';
import {SetTvTime} from '../../services/firebase';
/*        const interval = setInterval(function() {
            console.log(playerjs.api("time"));
        }, 5000);
*/
const Player = ({data}) =>{
    const video = getVideo(data);
    if(data.movie){
        return <Video url={data.url}/>
    }
    return(
        <Suspense fallback={<PlayerLoading/>}>
            <Video promise={video} data={data}/>
        </Suspense>
    );
}

const Video = ({promise,url,data}) =>{
    const resource = promise.read();
    const {currentUser} = useContext(AuthContext);
    let time = 0.0;
    function receiveMessage(event){
        if(currentUser){
            time = event.data.time;
        };
    };

    useEffect(() => {
        window.addEventListener('message', receiveMessage);
        const loop = setInterval(function(){
            if(currentUser){
                SetTvTime(currentUser.uid,'tv',
                data.id,data.resid,data.season,data.episode,
                time,data.translator,data.res,
                data.name,data.poster);
            }
        }, 2000);
    
        return () => {
          window.removeEventListener('message', receiveMessage);
          clearInterval(loop);
        };
    });

    return(
        <div className='playerjs'>
            <iframe 
            id="playerplayer"
            title='playerjs'
            allowFullScreen
            className='player fade-in'
            src={'/playerjs.html?file='+(resource.url || url)+(data.time ? `&start=${data.time}` : '')}
            type='text/html'
            allowtransparency='true'
            />
        </div>
    );
}
export default Player;